body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.SearchBar {
    flex: 1 1;
    /* background-color: rgb(40, 44, 52, 0.75); */
    /* border-radius: 8px; */
    /* margin: 8px; */
    /* padding: 16px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: flex-start; */
}
.SearchBar form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 100%;
}
.SearchBar input[type="text"] {
    margin: 8px;
    border-radius: 8px;
    border: none;
    padding: 4px;
    text-align: center;
    background-color: #1d282b;
    color: white;
    width: 80%;
    min-width: 20em;
    font-weight: 500;
}
.SearchBar input[type="text"]::-webkit-input-placeholder {
    font-style: italic;
    font-weight: 200;
    color: gray;
}
.SearchBar input[type="text"]::placeholder {
    font-style: italic;
    font-weight: 200;
    color: gray;
}
.SearchBar input[type="text"]:focus {
    background-color: #fbfbfb;
    color: black;
    transition: background-color .5s;
}
.SearchBar button {
    margin-top: 8px;
    width: 50%;
    min-width: 10em;
}
.Track {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.TrackDetails {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.TrackDetails .TrackTitle {
    font-size: 0.75em;
    max-width: 25em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.TrackDetails .TrackSubtitle {
    font-size: 0.5em;
    padding: 0.5em 1em 0.5em 0.5em;
}
.TrackDetails .TrackSubtitle span + span {
   margin-left: 1em;
}
.TrackDetails .TrackSubtitle .Artist,
.TrackDetails .TrackSubtitle .Album {
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.TrackDetails .TrackSubtitle .Artist {
    max-width: 20em;
}
.TrackDetails .TrackSubtitle .Album {
    color: gray;
    max-width: 18em;
}
.Track button {
    
}
.TrackList {
    list-style-type: none;
    display: flex;
    padding: 8px;
    margin: 8px;
    border-radius: 8px;
    background-color: #1d282b;
}
.SearchResults .TrackList {
    flex-direction: column;
    width: 100%;
}
.Playlist .Tracklist {
    flex-direction: row;
}
.SearchResults {
    flex: 2 1;
    padding: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.SearchResults .TrackList {
    height: 15em;
    overflow-y: auto;
}
.SearchResults .Track {
    display: flex;
    flex-direction: row;
    padding: 8px;
    justify-content: space-between;
}
.SearchResults .Track + .Track {
    border-top: 2px solid #3c3c3c;
}
.Playlist {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}
.Playlist form { flex: 1 1; }
.Playlist input[type="text"] {
    margin: 8px;
    border-radius: 8px;
    border: none;
    padding: 4px;
    text-align: center;
    background-color: #1d282b;
    color: white;
    width: 80%;
    min-width: 20em;
    font-weight: 500;
}
.Playlist input[type="text"]::-webkit-input-placeholder {
    font-style: italic;
    font-weight: 200;
    color: gray;
}
.Playlist input[type="text"]::placeholder {
    font-style: italic;
    font-weight: 200;
    color: gray;
}
.Playlist input[type="text"]:focus {
    background-color: #fbfbfb;
    color: black;
    transition: background-color .5s;
}
.Playlist .TrackList {
    flex: 2 1; 
    overflow-x: auto;
    height: 3em;
}
.Playlist .Track {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 8px;
    min-width: 15em;
    max-width: 15em;
}
.Playlist .Track + .Track {
    border-left: 2px solid #3c3c3c;
}
h2 { font-size: 16px; }

button {
    border: none;
    border-radius: 16px;
    cursor: pointer;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    transition: background-color 0.1s;
    height: 2.5em;
}
button.standard {
    background-color: #1a972b;
    padding: 8px 16px;
    min-width: 5em;
    width: 15em;
}
button.standard:disabled {
    background-color: gray;
    color: lightgray;
    cursor: not-allowed;
}
button.subtle {
    background-color: transparent;
    padding: 4px 4px;
    min-width: 3em;
    color: #1a972b;
}
button:hover {
    background-color: #6ae77b;
    color: white;
}

.App {
    background-color: #0f3c2e;
    text-align: center;
}

.App-header {
    background-color: #1d2122;
    min-height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: white;
    height: 10vmin;
}
.App-header .mmm {
    display: inline;
    color: #6ae77b;
}
.App-header h2 {
    margin-left: 1em;
    font-style: italic;
    color: lightgray;
}
.App-content-wrapper {
    overflow: hidden;
    position: relative;
    height: 90vmin;
}
.App-content-background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
    opacity: 0.1;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}
.App-content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px;
    color: white;
    font-size: calc(10px + 2vmin);
}
.App-content-section {
    display: flex;
    flex-direction: row;
    width: 90%;

    background-color: rgb(40, 44, 52, 0.75);
    border-radius: 8px;
    margin: 8px;
    padding: 8px;
}
.App-content-section:nth-child(1) { flex: 2 1; }
.App-content-section:nth-child(2) { flex: 1 1; }


